body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{background:#111414;background:linear-gradient(to right, #111817 0%, #1a2e29 40%, #1a2e29 60%, #111817 100%);filter:progid:DXImageTransform.Microsoft.gradient( startColorstr="#111414", endColorstr="#1b2221",GradientType=1 );height:auto}
.home-box{background:#111414;background:linear-gradient(to right, #111414 0%, #172824 40%, #172824 60%, #111414 100%);filter:progid:DXImageTransform.Microsoft.gradient( startColorstr="#111414", endColorstr="#1b2221",GradientType=1 );height:100vh;display:flex;flex-direction:column;justify-content:space-between}.home-links-box{display:flex;flex-direction:column;align-items:center;justify-content:center;width:100%;min-height:200px;z-index:2;padding-top:50px}.particles{position:absolute;width:100%;height:100%}.intro{font-family:"Quicksand";align-self:center;text-align:center;color:#1a8a6c;font-size:100%}.intro h1{color:#b5d1c7}.intro h3{opacity:.7;color:#e68619;text-shadow:0 0 3px red}@media(min-width: 812px){.home-links-box{flex-direction:row;min-height:auto}}
header{margin:0 auto;padding:10px 0;width:100vw;display:inline-flex;justify-content:space-between}.wrapper-svg{width:10vw;height:auto}
.nav-box{width:10vw;height:auto;display:flex;flex-direction:column;justify-content:center}.nav-svg{width:calc(10px + 1vw);margin:auto;display:block;padding:auto;fill:#b5d1c7}.nav-svg:hover{fill:#99aca5;cursor:pointer}.nav-list{display:flex;flex-direction:column;align-items:flex-end;justify-content:center;width:auto;margin-top:10px}.nav-link{padding:5px;width:auto}.nav-link a{font-family:"Quicksand";font-weight:bold;text-transform:uppercase;-webkit-text-emphasis:none;text-emphasis:none;text-decoration:none;color:#17473d;padding:2px;border:solid 2px transparent;background-color:rgba(16,20,17,.349)}.nav-link a:hover{color:#b5d1c7;text-shadow:0 0 2px #2abb86}@media(min-width: 812px){.nav-list{align-items:center;width:auto}.nav-link a{font-size:110%}}
.logo-svg{width:calc(20px + 1vw);margin:0 auto;display:block;opacity:1}.logo-svg:hover{opacity:.8;cursor:pointer}.logo-svg .svg-style{fill:#b5d1c7}
.home-link{display:flex;justify-content:center;align-items:center;width:110px;height:70px;font-size:130%;margin:0 30px}.home-link a{color:#17473d;text-decoration:none;font-family:"Quicksand";font-weight:bold;height:100%;padding:0 1px 0 1px;text-transform:uppercase}.home-link a:hover{color:#b5d1c7;text-shadow:0 0 10px #2abb86}.home-link span{color:#b5d1c7;height:100%}.home-link:hover span{color:#17473d}@media(min-width: 812px){.home-link a:hover{border-color:#b5d1c7;border-bottom:solid 3px;background:linear-gradient(to top, rgba(13, 194, 155, 0.6) 0%, rgba(34, 121, 106, 0.7) 5%, rgba(34, 66, 60, 0.7) 20%, rgba(0, 0, 0, 0) 40%);transition-duration:.4s;transition-timing-function:ease-in-out}}
.about-box{height:auto;width:100%;margin:0 auto;margin-bottom:0px;padding:20vh 0;background-color:rgba(9,12,12,.35);color:#b5d1c7;display:flex;flex-direction:column;align-items:center}.about-box .picture{height:200px;width:150px;margin:0 50px;background-image:url(/wannes-portfolio/static/media/wannes.e0dced35.jpg);background-size:cover;box-shadow:0 0px 10px #0f1413;border-radius:5px}.about-box .wrapper-about{font-family:"Quicksand";letter-spacing:1px;margin:0 30px}.about-box .wrapper-about h3{text-align:center;color:#1a8a6c}.about-box .wrapper-about h4{color:#18584a;margin-bottom:0;font-style:italic}.about-box .wrapper-about ul li{font-family:"Nunito";color:#18584a}@media(min-width: 812px){.about-box{flex-direction:row;justify-content:center}.about-box .picture{margin-right:20px}.about-box .wrapper-about{width:auto}.about-box .wrapper-about h3{text-align:start}}
.content-container{margin:50px auto}.content-box{margin:0 auto;padding:10px}.content-items{height:auto}.type-title{background:#111414;background:linear-gradient(to right, rgba(15, 24, 24, 0.1) 0%, rgba(9, 22, 20, 0.4) 20%, rgba(9, 22, 20, 0.4) 80%, rgba(15, 24, 24, 0.1) 100%);filter:progid:DXImageTransform.Microsoft.gradient( startColorstr="#111414", endColorstr="#1b2221",GradientType=1 );font-size:400%;text-align:center;margin:50px 0;color:#b5d1c7;text-transform:uppercase;height:auto;border-right:none;border-left:none}@media(min-width: 812px){.content-items{display:flex;flex-flow:row wrap;justify-content:space-evenly}.content-items .viewbox{width:48%}.type-title{width:100%}.content-box{width:80%;padding:0}}@media(min-width: 1661px){.content-items .viewbox{width:30%}}
.viewbox{height:auto;width:100%;padding:15px;display:flex;flex-direction:column;justify-content:space-between;border:solid 2px #1b2e28;border-radius:15px;margin:20px auto;background:linear-gradient(to bottom, rgba(9, 22, 20, 0.4) 0%, rgba(0, 0, 0, 0.05) 100%)}.item-title h1{width:100%;margin-top:0;font-size:170%;color:#1a8a6c}.item-info p{margin:2% 0;color:#b5d1c7}.item-links p{display:flex;flex-direction:row;justify-content:space-evenly;align-items:center}.item-links p a{height:auto;background:linear-gradient(to bottom, rgba(9, 22, 20, 0.4) 0%, rgba(0, 0, 0, 0) 100%);border:solid #2e463e 2px;border-radius:10px;color:#1a8a6c;text-decoration:none;font-size:110%;padding:5px;box-shadow:0 0px 5px #0f1413}.item-links p a:hover{box-shadow:inset 0 0px 5px #0f1413;color:#17725a}.item-img{width:100%;height:auto;overflow:hidden}@media(max-height: 567px),(min-width: 812px){.viewbox{flex-wrap:wrap}}
.viewlist{height:150px;width:100%;display:flex}.viewlist-button{width:calc(3vw + 20px);height:150px;background-image:linear-gradient(to right, rgba(117, 216, 186, 0.22) 0%, rgba(18, 228, 147, 0) 100%)}.viewlist-button:last-of-type{background-image:linear-gradient(to left, rgba(117, 216, 186, 0.22) 0%, rgba(18, 228, 147, 0) 100%)}.viewlist-box{height:100%;width:100%;display:flex;overflow-x:scroll;align-content:flex-start;margin:auto 0}.viewlist-item{height:auto;width:30%;margin:auto 10px;color:#1a8a6c;overflow:hidden}@media(max-height: 567px),(min-width: 812px){.viewlist{display:flex;flex-direction:column;width:15%;height:98%;margin:auto 0}.viewlist-box{flex-direction:column;height:100%;width:100%;overflow-y:scroll;overflow-x:none;justify-content:flex-start}.viewlist-item{margin:5px auto;width:200px;height:auto;overflow:hidden}.viewlist-button{width:100%;height:calc(3vw + 10px)}}
.contact-box{width:100%;height:auto;display:flex;flex-wrap:wrap;justify-content:center;padding:100px;box-shadow:0 5px 5px #0f1413}.socialmedia-svg{width:calc(30px + 2vw);height:calc(30px + 2vw);border:solid 2px #2e463e;border-radius:15px;box-shadow:0 1px 5px #0f1413;background:linear-gradient(to bottom, rgba(9, 22, 20, 0.4) 0%, rgba(0, 0, 0, 0) 100%);fill:#b5d1c7;padding:10px;margin:10px}.socialmedia-svg:hover{fill:#99aca5;border:solid 2px #2e463e;box-shadow:inset 0 0px 5px #0f1413;cursor:pointer}
.footer-box{width:100%;height:auto;padding:20px;background-color:rgba(9,12,12,.35);display:flex;flex-direction:column;justify-content:center;align-items:center}.footer-box p{color:#99aca5;margin:10px}.footer-box p span,.footer-box p a{color:#17725a}.footer-box p span:hover,.footer-box p a:hover{color:#2e463e}.footer-section{border:solid 2px #1e2522;background:linear-gradient(to bottom, rgba(9, 22, 20, 0.4) 0%, rgba(0, 0, 0, 0) 100%);height:auto;margin:10px}@media(min-width: 812px){.footer-box{flex-direction:row}.footer-section{min-height:150px}}
